export default {
	name: 'product-item',
	props: {
		list: {
			// 要显示的数组
			type: Array,
			default() {
				return [];
			}
		},
		fromShop: {
			// 是否是详情
			type: Boolean,
			default() {
				return false;
			}
		},
		nodata: {
			// 是否是详情
			type: Boolean,
			default() {
				return false;
			}
		},
		nomore: {
			// 是否是详情
			type: Boolean,
			default() {
				return false;
			}
		},
	},
	data() {
		return {};
	},
	methods: {
		showPic(src) {
			this.$acFrame.Util.showBigPic(src, [src]);
		},
		choosePord(obj){
			 this.$emit('setProduct',obj)
			/* uni.navigateTo({
				 url: 'productDetail?id='+obj.goodsId
			}); */
		},
		setImg(src){
			return  this.$acFrame.Util.setImgUrl(src);
		},
	}
};
